footer{
    background:#232323;
    padding: 3rem 0;
    text-align: center;
    font-size: 0.9rem;
  

    position:absolute;
    left:0;
    right:0;
    
    z-index:150;
    background-color:#232323;
    margin-top:250px;
    @media screen and (max-width:420px){
        margin-top:1600px;
    }
}

footer a{
    color:red;
}

.footer__logo{
    font-size: 2rem;
    font-weight: 500;
    margin-bottom: 2rem;
    display: inline-block;
    color: white;
}

.permalinks{
    display: flex;
    flex-wrap:wrap ;
    justify-content: center;
    grid-area: 2rem;
    gap: 2rem;
    margin: 0 auto 3rem;
}

.footer__socials{
    display: flex;
    justify-content: center;
    gap: 1rem;
    margin-bottom: 4rem;
}

.footer__socials a{
    background:black;
    color: white;
    padding: 0.8rem;
    border-radius: 0.7rem;
    display: flex;
    border: 1px solid transparent;
}

.footer__socials a:hover{
    background: transparent;
    color: red;
    border-color: #232323;
}

.footer__copyright{
    margin-bottom: 4rem;
    color: white;
}

@media screen and (max-width: 600px) {
   .permalinks{
    flex-direction: column;
    gap: 1.5rem;
   }
   .footer__socials{
    margin-bottom: 2.6rem;
   }
}
p{
    color: white;
}
.logoImage{
    position: absolute;
    height: 350px;
    width: 350px;
    padding: 20px;

   
    left: 0;
  
    margin-left: 50px;
    @media screen and (max-width:1240px){
        margin-right:500px;
    }
}